
import { ReactNode } from 'react';
import { Slot } from '@radix-ui/react-slot';
import { clsx } from 'clsx';

export interface ButtonProps {
    children: ReactNode,
    asChild?: boolean,
}

export function Button({ children, asChild }: ButtonProps) {
    const Tag = asChild ? Slot : 'button'

    return (
        <Tag className={clsx(
            'bg-cyan-500 text-black font-semibold py-4 px-3 rounded text-sm w-full transition-colors hover:bg-cyan-300 focus:ring-2 ring-white',
        )}
        >
            { children }
        </Tag>
    )
}